@keyframes textLineAnimation {
  0% {
    transform: scaleX(1);
  }

  100% {
    transform: scaleX(0);
  }
}

.section {
  &_wrapper {
    width: 100%;
    display: flex;
    align-items: center;
  }
  &_img {
    width: 50%;
    height: 20rem;
    border: 1px solid var(--darkGreen);
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &_content {
    width: 50%;
    padding: 2rem;
    p {
      text-align: justify;
    }
  }
  &_title {
    text-align: center;
    font-size: 1.7rem;
    padding-bottom: 1rem;
    position: relative;
    color: var(--sage);
    display: flex;
    justify-content: center;
    &::after {
      content: '';
      position: absolute;
      width: 60%;
      transform: scaleX(1);
      height: 1px;
      bottom: 0;
      animation: textLineAnimation 2s ease 0s 1 reverse none;
      z-index: 9;
      background-color: var(--sage);
    }
  }
  &_corousel {
    display: flex;
    width: 100%;
    height: 15rem;
    margin: 1rem 0;
    padding: 1rem 0;
  }
}

@media screen and (max-width: 1440px) {
  .section {
    &_corousel {
      height: 12rem;
    }
  }
}

@media screen and (max-width: 768px) {
  .section {
    &_corousel {
      height: 9rem;
    }
    &_title {
      font-size: 1.4rem;
    }
    &_content {
      padding: 0 1rem;
      p {
        font-size: 0.9rem;
      }
    }
  }
}

@media screen and (max-width: 615px) {
  .section {
    &_wrapper {
      flex-direction: column;
    }
    &_img {
      width: 100%;
    }
    &_content {
      width: 100%;
    }
  }
}
